if ('addEventListener' in window) {
    window.addEventListener('load', function () {
        document.body.className = document.body.className.replace(/\bis-preload\b/, '');
    });
    document.body.className += (navigator.userAgent.match(/(MSIE|rv:11\.0)/) ? ' is-ie' : '');

    // ganti tanggal otomatis
    getDate();
}

function getDate() {
    const date = new Date();
    const stringTahun = date.getFullYear();
    const documentDate = document.getElementById("date_copyright");
    const dateCopyright = stringTahun + ' &copy; Diskominfo Bandung'
    documentDate.innerHTML = dateCopyright;
}